.App {
  font-family: sans-serif;
  text-align: center;
  background-color: #1a1b1d;
  color: white;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.card {
  /* Add shadows to create the "card" effect */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 400px;
  background-color: #101011;
  margin: 10px;
}

.container {
  padding: 2px 16px 16px;
}

.calls {
  display: flex;
}
